/* dashboard section css start */
.widget-card {
  padding: 15px;
  background-color: rgba(#7367f0, 0.15);
  border: 1px solid rgba(#7367f0, 0.55);
  @include border-radius(5px);
  position: relative;
  overflow: hidden;
  @include d-flex;
  align-items: center;
  &__header {
    width: 100%;
    padding-bottom: 6px;
    border-bottom: 1px solid rgba(#7367f0, 0.25);
    @include d-flex;
    margin-bottom: rem(20px);
    .title {
      width: calc(100% - 47px);
      padding-right: 10px; }
    .view-btn {
      width: 47px;
      padding: 2px 10px;
      background-color: #7367f0;
      color: #fff;
      @include border-radius(3px);
      font-size: rem(12px); } }
  &__content {
    width: calc(100% - 60px);
    padding-right: rem(15px);
    .widget-number {
      font-family: $heading-font;
      font-weight: 600;
      font-size: rem(28px);
      line-height: 1.2; } }
  &__icon {
    width: 55px;
    height: 55px;
    background-color: rgba(#7367f0, 0.85);
    @include border-radius(5px);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(24px);
    color: #fff; }
  &.widget-color--2 {
    background-color: rgba(#1e9ff2, 0.15);
    border: 1px solid rgba(#1e9ff2, 0.55);
    .widget-card__icon,
    .widget-card__header .view-btn {
      background-color: #1e9ff2; }
    .widget-card__header {
      border-bottom: 1px solid rgba(#1e9ff2, 0.25); } }
  &.widget-color--3 {
    background-color: rgba(#009688, 0.15);
    border: 1px solid rgba(#009688, 0.55);
    .widget-card__header {
      border-bottom: 1px solid rgba(#009688, 0.25); }
    .widget-card__icon,
    .widget-card__header .view-btn {
      background-color: #009688; } }
  &.widget-color--4 {
    background-color: rgba(#4caf50, 0.15);
    border: 1px solid rgba(#4caf50, 0.55);
    .widget-card__header {
      border-bottom: 1px solid rgba(#4caf50, 0.25); }
    .widget-card__icon,
    .widget-card__header .view-btn {
      background-color: #4caf50; } }
  &.widget-color--5 {
    background-color: rgba(#00bcd4, 0.15);
    border: 1px solid rgba(#00bcd4, 0.55);
    .widget-card__header {
      border-bottom: 1px solid rgba(#00bcd4, 0.25); }
    .widget-card__icon,
    .widget-card__header .view-btn {
      background-color: #00bcd4; } }
  &.widget-color--6 {
    background-color: rgba(#0779e4, 0.15);
    border: 1px solid rgba(#0779e4, 0.55);
    .widget-card__header {
      border-bottom: 1px solid rgba(#0779e4, 0.25); }
    .widget-card__icon,
    .widget-card__header .view-btn {
      background-color: #0779e4; } } }

.table-ad {
  @include d-flex;
  align-items: center;
  .thumb {
    width: 55px;
    background-color: #f1f1f1;
    padding: 3px;
    @include border-radius(5px); }
  .content {
    width: calc(100% - 55px);
    padding-left: rem(15px);
    .title {
      font-size: rem(16px);
      font-weight: 700; } } }
.account-header {
  padding: rem(20px);
  @include border-radius(5px);
  background-color: $base-color-two;
  .thumb {
    overflow: hidden;
    @include border-radius(5px);
    img {
      width: 100%; } }
  .content {
    margin-top: rem(25px);
    .name {
      color: #fff;
      font-size: rem(20px); }
    .account-info-list {
      li {
        color: #fff;
        border-color: rgba(#fff, 0.25);
        i {
          color: $base-color; } } } } }
.account-info-list {
  li {
    padding: rem(10px) 0;
    border-bottom: 1px dashed $border-color;
    @include d-flex;
    align-items: center;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0;
      border-bottom: none; }
    i {
      width: 30px;
      font-size: rem(22px); }
    span {
      width: calc(100% - 30px);
      word-break: break-word; } } }
.account-menu-wrapper {
  padding: rem(20px);
  margin-top: rem(30px);
  border: 1px solid $border-color;
  @include border-radius(5px);
  background-color: #fff;
  @include media(991px) {
    position: fixed;
    top: 35px;
    left: -105%;
    z-index: 99;
    @include transition(all 0.5s);
    &.active {
      left: 0; } } }
.account-sidebar-close-btn {
  position: absolute;
  top: -2px;
  right: -2px;
  width: 30px;
  height: 30px;
  background-color: $danger;
  color: #fff;
  @include border-radius(3px);
  display: none;
  @include media(991px) {
    display: inline-block; } }
.account-sidebar-open-btn {
  background-color: $base-color-two;
  color: #fff;
  padding: rem(8px) rem(25px);
  @include border-radius(3px);
  margin-bottom: rem(20px);
  display: none;
  @include media(991px) {
    display: inline-block; } }
.account-menu {
  .menu-header {
    font-weight: 500;
    margin-bottom: 10px;
    margin-top: 30px;
    font-size: rem(14px);
    color: #777; }
  li {
    &.active {
      a {
        background-color: rgba($base-color, 0.15);
        color: $base-color-two;
        @include border-radius(5px);
        border-bottom: none;
        &:hover {
          color: $base-color-two; } } }
    a {
      color: $para-color;
      padding: 10px 20px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      border-bottom: 1px dashed $border-color;
      &:hover {
        color: $base-color; }
      i {
        font-size: rem(22px);
        width: 25px; }
      .menu-title {
        width: calc(100% - 25px);
        padding-left: 5px; } } } }

.create-ad-wrapper {
  padding: 50px;
  border: 2px solid $border-color;
  @include border-radius(5px);
  @include media(480px) {
    padding: rem(40px) rem(30px); }
  @include media(360px) {
    padding: rem(30px) rem(20px); }
  .title {
    padding-bottom: rem(10px);
    border-bottom: 1px solid $border-color;
    margin-bottom: rem(25px); } }
/* dashboard section css end */
