/* footer section css start */
.footer {
  background-color: $base-color-two;
  position: relative;
  z-index: 1;
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.85;
    z-index: -1; }
  &__top {
    padding-top: 70px;
    padding-bottom: 50px; }
  .footer-support {
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(#fff, 0.15); }
  p, a, span, li, i {
    color: darken(#fff, 5%); }
  &__bottom {
    padding: 20px 0;
    position: relative;
    z-index: 1;
    &::after {
      @include positionTwo;
      background-color: darken($base-color-two, 2%);
      z-index: -1; } } }
.footer-logo {
  max-height: 55px; }
.footer-support-single {
  @include media(575px) {
    display: flex;
    align-items: center; }
  @include media(380px) {
    display: block; }
  .caption {
    color: #fff;
    font-size: rem(18px);
    @include media(575px) {
      padding-right: 10px; }
    & ~ a {
      @include media(575px);
      margin-top: 0 !important; } } }

.footer-widget {
  &__title {
    color: #fff;
    font-size: rem(18px);
    margin-bottom: rem(35px);
    font-weight: 600;
    @include media(991px) {
      margin-bottom: rem(20px); } }
  .link-list {
    li {
      a {
        padding: 5px;
        display: block;
        border-color: rgba(#fff, 0.25); } } }
  .social-link-list {
    li {
      a {
        background-color: $base-color; } } } }

.link-list {
  li {
    &:first-child {
      a {
        padding-top: 0; } }
    &:last-child {
      a {
        padding-bottom: 0;
        border-bottom: none; } }
    a {
      padding: 7px 0;
      display: block;
      border-bottom: 1px dashed $border-color;
      &:hover {
        color: $base-color; } } } }
.social-link-list {
  @include d-flex;
  margin: -5px -7px;
  li {
    margin: 5px 7px;
    a {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $base-color-two;
      color: #fff;
      @include border-radius(5px);
      font-size: 20px; } } }
/* footer section css end */
