/* card css start */
.custom--card {
  background-color: #fff;
  box-shadow: none;
  border: 1px solid #d6d6d6;
  @include border-radius(5px);
  .card-header {
    background-color: #4582ff08; }
  .card-body {
    padding: rem(20px); } }
.card {
  border-width: 2px;
  &.style--two {
    background-color: transparent;
    .card-header {
      padding: rem(15px) rem(25px);
      background-color: transparent; }
    .card-body {} } }
/* card css end */
