/* main search section css start */
.main-search-area {
  padding: rem(35px) 0;
  border-bottom: 1px solid $border-color; }
.advance-btn {
  text-align: center;
  padding: rem(12px) rem(20px);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: #f1f1f1;
  border: 1px solid $border-color;
  @include border-radius(5px);
  i {
    font-size: rem(24px); } }

.advance-search-area {
  padding: rem(30px);
  background-color: #06204512;
  margin-top: rem(30px);
  display: none;
  @include media(575px) {
    padding: rem(15px); } }
/* main search section css end*/

/* feature listing section css start */
.property-card {
  padding: rem(15px);
  background-color: #fff;
  @include border-radius(5px);
  box-shadow: 0 0 10px rgba(#000, 0.1);
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 20px rgba(#000, 0.15);
    @include transform(translateY(-5px)); }
  &__thumb {
    position: relative;
    height: 280px;
    a {
      height: 100%; }
    img {
      width: 100%;
      height: 100%;
      @include object-fit; }
    .property-status {
      font-size: rem(14px);
      position: absolute;
      top: 10px;
      right: 10px;
      padding: rem(2px) rem(15px);
      background-color: $base-color;
      color: #fff;
      @include border-radius(5px); } }
  &__content {
    margin-top: rem(20px);
    .title {
      font-size: rem(22px); } }
  .location {
    margin-top: rem(5px);
    font-size: rem(15px); }
  .property-meta {
    margin-top: rem(20px);
    @include d-flex;
    li {
      font-size: rem(15px);
      width: 50%;
      padding: rem(10px) 0;
      border-bottom: 1px dashed $border-color;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      i {
        font-size: rem(18px);
        margin-right: rem(5px); }
      &:nth-last-child(-n + 2) {
        border-bottom: none; } } }
  &__footer {
    @include d-flex;
    align-items: center;
    justify-content: space-between;
    padding: rem(10px) rem(15px);
    margin-top: rem(10px);
    background-color: #f7f7f7;
    @include border-radius(5px);
    .price {
      font-family: $heading-font;
      font-size: rem(24px);
      font-weight: 600;
      color: $base-color; }
    .ratings {
      @include d-flex;
      align-items: center;
      i {
        color: #F9BF00; } } } }
/* feature listing section css end */

/* new listing section css start */
.new-list-slider {
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 45px;
    height: 45px;
    margin-top: -22px;
    background-color: $base-color;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: rem(22px);
    z-index: 1;
    @include border-radius(50%);
    @include media(991px) {
      top: auto;
      bottom: -50px; }
    &.prev {
      left: -35px;
      @include media(991px) {
        left: calc(50% - 50px); } }
    &.next {
      right: -35px;
      @include media(991px) {
        right: calc(50% - 50px); } } }
  .slick-list {
    margin: 0 rem(-8px); }
  .single-slide {
    margin: 0 rem(8px); } }
.new-list-card {
  overflow: hidden;
  @include border-radius(8px);
  position: relative;
  z-index: 1;
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 10px 1px rgba(#000, 0.15);
    @include transform(translateY(-5px)); }
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.65; }
  &__thumb {
    height: 320px;
    img {
      width: 100%;
      height: 100%;
      @include object-fit; } }
  &__content {
    position: absolute;
    padding: rem(20px);
    width: 100%;
    bottom: 0;
    left: 0;
    .title {
      font-size: rem(22px);
      color: #fff; }
    .location {
      color: #fff;
      margin-top: rem(5px); } }
  &__footer {
    @include d-flex;
    align-items: center;
    justify-content: space-between;
    padding: rem(5px) rem(20px);
    @include border-radius(5px);
    background-color: rgba(#fff, 0.15);
    margin-top: rem(20px);
    .price {
      font-family: $heading-font;
      font-size: rem(24px);
      font-weight: 600;
      color: $base-color; }
    .ratings {
      color: #fff;
      i {
        color: #F9BF00; } } } }
/* new listing section css end */

/* cta-section css start */
.cta-wrapper {
  padding: rem(70px) rem(50px);
  @include border-radius(5px); }
/* cta-section css end */

/* location list section css start */
.location-card {
  position: relative;
  @include border-radius(5px);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  min-height: 250px;
  height: 100%;
  z-index: 1;
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 15px 1px rgba(#000, 0.15);
    @include transform(translateY(-5px)); }
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.55;
    z-index: -1; }
  .place-name {
    color: #fff;
    font-size: rem(24px); }
  .list-amount {
    color: #fff;
    &::before {
      content: '('; }
    &::after {
      content: ')'; } } }
/* location list section css end */

/* agent section css start */
.agent-card {
  padding: rem(15px);
  background-color: #fff;
  @include border-radius(5px);
  box-shadow: 0 0 10px rgba(#000, 0.1);
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 15px rgba(#000, 0.15);
    @include transform(translateY(-5px)); }
  &__content {
    margin-top: rem(15px);
    padding: 0 rem(10px); } }
.ratings {
  i {
    color: #F9BF00; } }
/* agent section css end */

/* blog section css start */
.post-card {
  padding: 10px;
  @include border-radius(5px);
  background-color: #fff;
  box-shadow: 0 0 10px rgba(#000, 0.1);
  @include transition(all 0.3s);
  &:hover {
    box-shadow: 0 5px 15px rgba(#000, 0.1); }
  &__thumb {
    box-shadow: 0 0 5px rgba(#000000, 0.15);
    @include border-radius(5px);
    img {
      width: 100%;
      @include border-radius(5px); } }
  &__content {
    padding: rem(10px);
    margin-top: rem(15px); } }
.post-meta {
  @include d-flex;
  margin: rem(-5px) rem(-10px);
  li {
    margin: rem(5px) rem(10px);
    a {
      color: $para-color;
      display: flex;
      align-items: center;
      &:hover {
        color: $base-color; }
      i {
        font-size: rem(18px); } } } }
.read-more {
  color: $base-color;
  text-decoration: underline;
  &:hover {
    text-decoration: underline; } }
/* blog section css end */


/* blog-details-section css start */
.blog-details__thumb {
  position: relative;
  img {
    width: 100%; }
  .post__date {
    position: absolute;
    bottom: -1px;
    right: 0;
    width: 75px;
    text-align: center;
    .date {
      font-size: 30px;
      font-weight: 700;
      color: #ffffff;
      background-color: $base-color;
      padding: 10px 5px;
      width: 100%;
      line-height: 1; }
    .month {
      background-color: #ffffff;
      text-transform: uppercase;
      padding: 4px 5px;
      width: 100%;
      line-height: 1;
      font-size: 18px; } } }
.blog-details__content {
  margin-top: 30px;
  p {
    margin-top: 20px; }
  .blog-details__title {
    font-size: 24px; }
  blockquote {
    margin-top: 30px;
    margin-bottom: 0; } }
blockquote {
  font-size: 18px;
  color: $para-color;
  font-style: italic;
  text-align: center;
  padding: 50px 60px;
  background-color: $base-color-two;
  color: #fff;
  @media (max-width: 575px) {
    padding: 30px 40px;
    font-size: 16px; } }
.blog-details__footer {
  text-align: center;
  padding: 50px 0;
  border-bottom: 1px solid #e5e5e5;
  .caption {
    font-size: 24px;
    margin-bottom: 20px; }
  .social__links {
    justify-content: center;
    @include d-flex;
    li {
      a {
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        color: $heading-color;
        &:hover {
          background-color: $base-color;
          color: #ffffff; } } } } }
.comments-area {
  margin-top: 100px;
  margin-right: 30px;
  @include border-radius(8px);
  .title {
    text-transform: capitalize;
    margin-bottom: 20px;
    font-size: 24px; } }
.comments-list {
  .single-comment {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; }
    &__thumb {
      width: 100px;
      height: 100px;
      overflow: hidden; }
    &__content {
      width: (calc(100% - 100px));
      padding-left: 20px;
      @media (max-width: 480px) {
        @include flexWidth(100%);
        padding-left: 0;
        margin-top: 15px; }
      .name {
        text-transform: capitalize; }
      .date {
        font-size: 14px;
        font-style: italic; }
      p {
        margin-top: 5px; } } } }
.reply-btn {
  width: 28px;
  height: 28px;
  background-color: #f7f5fb;
  color: $base-color;
  text-align: center;
  line-height: 28px;
  font-size: 13px;
  float: right;
  margin-top: -30px;
  @include border-radius(50%);
  &:hover {
    background-color: $base-color;
    color: #ffffff; }
  i {
    @include transform(rotate(180deg)); } }
.comment-form-area {
  margin-top: 70px;
  .title {
    font-size: 24px;
    margin-bottom: 20px; } }
.comment-form {
  input, textarea {
    background-color: #ffffff;
    border: 1px solid #e5e5e5;
    &:focus {
      box-shadow: 0px 3px 18px 2px rgba(46, 25, 204, 0.16);
      border-color: transparent; } } }
/* blog-details-section css end */


/* account section css start */
.account-form {
  background-color: $base-color-two;
  .title {
    font-size: rem(32px);
    color: #fff; }
  label {
    color: darken(#fff, 2%); }
  .form--control {
    background-color: transparent;
    border-color: rgba(#fff, 0.25);
    @include placeholder(darken(#fff, 10%));
    color: #fff; }
  .input-group-text {
    background-color: $base-color;
    color: #fff;
    border-color: $base-color; }
  p {
    color: #fff; } }
.box--border {
  border: 4px solid rgba($base-color, 0.55); }
.box--shadow {
  box-shadow: 0 10px 25px rgba($base-color, 0.15); }
.z-index {
  z-index: 1; }
.bottom-img {
  position: absolute;
  bottom: 0;
  left: 0;
  opacity: 0.25;
  z-index: -1; }
/* account section css end */

/* contact section css start */
.single-info {
  $icon: rem(85px);
  &__icon {
    width: $icon;
    height: $icon;
    font-size: calc(#{$icon} / 2);
    background-color: $base-color-two;
    @include media(991px) {
      width: rem(75px);
      height: rem(75px); }
    @include media(380px) {
      width: rem(60px);
      height: rem(60px);
      font-size: calc(#{rem(60px)} / 2); } }
  &__content {
    width: calc(100% - #{$icon});
    padding-left: rem(25px);
    .title {
      font-size: rem(28px);
      @include media(991px) {
        font-size: rem(24px); }
      @include media(380px) {
        font-size: rem(22px); } } } }
.contact-info-list {
  .single-info {
    padding: rem(20px) 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; } } }
.contact-form {
  background-color: $base-color-two;
  .title {
    font-size: rem(32px);
    color: #fff; }
  label {
    color: darken(#fff, 2%); }
  .form--control {
    background-color: transparent;
    border-color: rgba(#fff, 0.25);
    @include placeholder(darken(#fff, 10%));
    color: #fff; }
  .input-group-text {
    background-color: $base-color;
    color: #fff;
    border-color: $base-color; }
  p {
    color: #fff; }
  select {
    background-color: transparent;
    color: #fff;
    border-color: rgba(#fff, 0.25);
    option {
      background-color: $base-color; } } }
/* contact section css end */


/* search result section css start */



.search-area {
  background-color: $base-color-two;
  padding: 20px 0;
  .search-form {
    background-color: transparent;
    &::before {
      display: none; }
    label {
      color: darken(#fff, 5%);
      margin-bottom: rem(8px); }
    .select2-container {
      .select2-selection--single {
        @include border-radius(5px);
        .select2-selection__arrow {
          right: 12px; }
        .select2-selection__rendered {
          padding-left: 20px; } } } } }

.card-view-btn-area {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  margin: rem(-5px) rem(-7px);
  button {
    width: 40px;
    height: 40px;
    background-color: #fff;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-size: rem(20px);
    margin: rem(5px) rem(7px);
    @include border-radius(5px);
    @extend %transition;
    box-shadow: 0 0 5px rgba(#000, 0.1);
    &.active {
      background-color: $base-color;
      color: #fff; } } }
.list-view {
  .card-view {
    width: 100% !important; }
  .property-card {
    @include d-flex;
    align-items: center;
    &__thumb {
      width: 30%;
      max-height: 200px;
      overflow: hidden;
      @include border-radius(5px);
      @include media(540px) {
        width: 100%; } }
    &__content {
      width: 70%;
      padding: 0 20px;
      margin-top: 0;
      @include media(540px) {
        width: 100%;
        margin-top: 20px;
        padding: 0 10px; }
      .bookmark-btn {
        right: auto;
        left: -50px;
        top: 5px; } } } }
.search-tab-menu {
  margin: rem(-3px);
  @include d-flex;
  li {
    padding: rem(3px);
    button {
      font-size: rem(14px);
      background-color: #fff;
      padding: rem(4px) rem(10px);
      border: 1px solid $border-color;
      &.active {
        background-color: $base-color;
        color: #fff; } } } }
/* search result section css end */


/* property details section css start */
.details-thumb-slider {
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 50px;
    height: 45px;
    background-color: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(22px);
    z-index: 1;
    margin-top: -25px;
    &:hover {
      background-color: $base-color;
      color: #fff; }
    &.prev {
      left: 10px; }
    &.next {
      right: 10px; } }
  .thumb-item {
    height: 450px;
    img {
      width: 100%;
      height: inherit;
      @include object-fit; } } }
.details-thumb-nav {
  .thumb-item {
    height: 120px;
    position: relative;
    &::after {
      @include positionTwo;
      background-color: $base-color-two;
      opacity: 0.55;
      @extend %transition; }
    img {
      width: 100%;
      height: inherit;
      @include object-fit; } }
  .single-slide {
    &.slick-current.slick-active {
      .thumb-item {
        &::after {
          opacity: 0; } } } } }
.property-details-meta {
  @include d-flex;
  margin: rem(-3px) rem(-8px);
  li {
    margin: rem(3px) rem(8px);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    i {
      font-size: rem(22px);
      margin-right: 4px;
      color: $base-color; } } }
.property-details-price {
  font-family: $heading-font;
  font-weight: 700;
  font-size: rem(32px);
  color: $heading-color; }
.property-details-box {
  padding: rem(20px);
  background-color: #fff;
  border: 2px solid $border-color;
  @include border-radius(5px);
  .title {
    font-size: rem(20px);
    margin-bottom: rem(15px); }
  .property-meta {
    @include d-flex;
    margin: rem(-5px);
    li {
      width: 25%;
      display: flex;
      align-items: center;
      padding: rem(5px);
      @include media(991px) {
        width: calc(100% / 3); }
      @include media(767px) {
        width: calc(100% / 2); }
      i {
        font-size: rem(24px);
        margin-right: 3px; } } } }
.amenities-list {
  @include d-flex;
  margin: rem(-5px);
  li {
    width: 25%;
    padding: rem(5px);
    position: relative;
    padding-left: 22px;
    @include media(991px) {
      width: calc(100% / 3); }
    @include media(767px) {
      width: calc(100% / 2); }
    &::before {
      position: absolute;
      top: 6px;
      left: 0;
      font-family: 'Line Awesome Free';
      font-weight: 900;
      content: "\f14a"; } } }
/* property details section css end */
