/* hero section css start */
.hero-slider-main {
  .single-slide {
    padding-top: 350px;
    padding-bottom: 350px;
    background-position-y: top;
    position: relative;
    z-index: 1;
    @include media(1399px) {
      padding-top: 280px;
      padding-bottom: 280px; }
    @include media(767px) {
      padding-top: 220px; }
    &::before {
      @include positionTwo;
      background-color: $base-color-two;
      opacity: 0.35;
      z-index: -1; } } }

.hero-slider-nav {
  margin-top: -175px;
  @include media(575px) {
    margin-left: 15px;
    margin-right: 15px; }
  .slick-list {
    margin: 0 rem(-10px); }
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 45px;
    height: 40px;
    margin-top: -20px;
    background-color: rgba($base-color-two, 0.85);
    border: 1px solid rgba(#fff, 0.25);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: rem(20px);
    z-index: 1;
    &.prev {
      left: -20px;
      @include media(575px) {
        left: 0; } }
    &.next {
      right: -20px;
      @include media(575px) {
        right: 0; } } }
  .single-slide {
    position: relative;
    margin: 0 rem(10px);
    height: 135px;
    overflow: hidden;
    background-color: $base-color-two;
    @include media(1399px) {
      height: 110px; }
    &::after {
      @include positionTwo;
      background-color: $base-color-two;
      opacity: 0.5;
      @extend %transition; }
    &.slick-current.slick-active {
      &::after {
        opacity: 0.1; } }
    img {
      width: 100%;
      height: 100%;
      @include object-fit; } } }


.hero-list-item {
  @include d-flex;
  position: relative;
  z-index: 1;
  &::after {
    position: absolute;
    content: '';
    left: -20px;
    bottom: -20px;
    width: 55%;
    height: 100%;
    background-color: $base-color-two;
    z-index: -1;
    opacity: 0.75;
    @include media(767px) {
      display: none; } }
  .left {
    width: 55%;
    background-color: $base-color;
    padding: rem(40px) rem(30px);
    @include media(767px) {
      width: 100%;
      text-align: center; } }
  .price {
    font-family: $heading-font;
    font-weight: 700;
    font-size: rem(48px);
    color: #fff;
    @include media(767px) {
      font-size: rem(42px); } }
  .title {
    color: #fff;
    @include media(767px);
    font-size: rem(20px); }
  .location {
    color: darken(#fff, 2%); }
  .right {
    width: 45%;
    background-color: rgba($base-color-two, 0.9);
    @include d-flex;
    @include media(767px) {
      width: 100%; }
    .single {
      height: 50%;
      width: 50%;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid rgba(#fff, 0.1);
      @include media(767px) {
        padding: 20px;
        height: auto; }
      &:nth-child(1),
      &:nth-child(3) {
        border-left: none; }
      &:nth-child(2),
      &:nth-child(4) {
        border-right: none;
        border-left: none; }
      &:nth-child(1),
      &:nth-child(2) {
        border-top: none;
        border-bottom: none; }
      &:nth-child(3),
      &:nth-child(4) {
        border-bottom: none; }
      i {
        color: $base-color;
        font-size: rem(42px);
        line-height: 1; }
      .caption {
        color: #fff;
        font-size: rem(15px);
        margin-top: rem(3px); } } } }
/* hero section css end */



/* inner-page-hero css start */
.inner-page-hero {
  padding-top: 140px;
  padding-bottom: 60px;
  position: relative;
  z-index: 1;
  background-position-y: center;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-one;
    opacity: 0.85;
    z-index: -1; }
  .page-title {
    font-weight: 600;
    color: #ffffff; }
  .page-list {
    @include d-flex;
    margin-top: 15px;
    li {
      padding: 0 15px;
      position: relative;
      color: rgba(#ffffff, 0.7);
      &:first-child {
        padding-left: 0;
        &::before {
          display: none; } }
      &::before {
        position: absolute;
        content: '/';
        color: #ffffff;
        left: 0px; }
      a {
        color: #ffffff; } } } }
/* inner-page-hero css end */
